.checkout-page {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background: #fafafa;
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0,0,0,0.1);
}

.checkout-page h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.checkout-form label {
  display: block;
  margin-bottom: 15px;
  font-weight: 600;
  color: #555;
}

.checkout-form input {
  width: 100%;
  padding: 8px 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  box-sizing: border-box;
}

.checkout-form button {
  width: 100%;
  padding: 12px;
  margin-top: 20px;
  background-color: #0077ff;
  color: white;
  font-weight: 700;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.checkout-form button:hover:not(:disabled) {
  background-color: #005fcc;
}

.checkout-form button:disabled {
  background-color: #a0c4ff;
  cursor: not-allowed;
}

.error {
  margin-top: 10px;
  color: #d93025;
  font-weight: 600;
  text-align: center;
}
