/* src/components/Header.css */
.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #34495e;
  padding: 10px 30px;
  height: 60px;
  color: white;
  box-sizing: border-box;
}

.left-menu a {
  color: #ecf0f1;
  margin-right: 20px;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  padding: 6px 10px;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.left-menu a:hover {
  background-color: #3b5998;
}

.left-menu a.active {
  background-color: #2980b9;
  font-weight: 700;
}

.center-categories {
  display: flex;
  gap: 15px;
}

.category-button {
  background-color: transparent;
  border: 2px solid #ecf0f1;
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.category-button:hover {
  background-color: #2980b9;
  border-color: #2980b9;
  color: white;
}
