/* src/components/ProductCard.css */
.product-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;  /* 여백을 좀 더 추가 */
  text-align: center;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  margin: 10px;  /* 카드 간 간격을 줄이기 위해 여백을 키움 */
  width: 260px; /* 카드 크기 더 키우기 */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product-card img {
  max-width: 100%;
  height: 180px;  /* 이미지 크기 키우기 */
  object-fit: contain;
  margin-bottom: 10px;
}

.product-card h3 {
  font-size: 1.1rem; /* 제목 크기 키우기 */
  margin: 6px 0; /* 제목과 가격 간격 늘리기 */
}

.product-card p {
  font-size: 0.95rem;  /* 설명 텍스트 크기 키우기 */
  color: #555;
  flex-grow: 1;
  margin: 4px 0;
}

.product-card .price {
  font-weight: bold;
  margin-top: 6px;
  font-size: 1.1rem;
  color: #2a9d8f;
}

.product-card button {
  margin-top: 10px;  /* 버튼과의 간격 더 늘리기 */
  padding: 8px 12px;
  background-color: #2a9d8f;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.product-card button:hover {
  background-color: #21867a;
}
